@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&family=Roboto:wght@100;300;400&family=Ubuntu:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

$blue: #2e5fdc;
$light-blue: #dcebf5;
$light-blue-2: rgb(123, 171, 242);
$m-left: 1.5em;

@mixin flex($jc) {
  display: flex;
  justify-content: $jc;
  align-items: center;
}

@mixin grid1() {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  grid-auto-rows: minmax(25px, auto);
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.createPost-body {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100vh;
  @include flex(center);
  flex-direction: column;
  background-color: $light-blue;
  .heading {
    margin: 2em 0;
    font-weight: 500;
    text-transform: uppercase;
  }
  .post-container {
    width: 75%;
    padding: 2em;
    background-color: $light-blue-2;
    border-radius: 5px;
    box-shadow: -12px 12px 10px rgb(232, 201, 201);
    margin-bottom: 5vh;
    .field + .field {
      margin-top: 6vh;
    }
    .field {
      p {
        width: 30%;
        text-align: right;
      }
    }
    .field:nth-child(1) {
      @include flex(left);
      input {
        font-size: 1rem;
        padding: 0.5em;
        margin-left: $m-left;
        width: 50%;
      }
    }
    .field:nth-child(2) {
      @include flex(left);
      p {
        width: 30%;
        text-align: right;
      }
      select {
        font-size: 1rem;
        padding: 0.5em;
        margin-left: $m-left;
        width: 30%;
      }
    }
    .field:nth-child(3) {
      display: flex;
      justify-content: left;
      textarea {
        font-size: 1rem;
        padding: 0.5em;
        margin-left: $m-left;
      }
    }
    .field:nth-child(4) {
      @include flex(left);
      input {
        font-size: 1rem;
        margin-left: $m-left;
      }
    }
    .field:nth-child(5) {
      @include flex(left);
      input {
        font-size: 1rem;
        padding: 0.5em;
      }
      .links {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: 1em;
        margin-left: 0.75em;
        p {
          text-align: center;
        }
        div {
          @include flex(center);
          flex-direction: column;
          input {
            margin-top: 0.5em;
          }
        }
        div:nth-child(2) {
          margin: 0 1vw;
        }
        div:nth-child(3) {
          margin-top: 1em;
        }
      }
    }
  }
  .circle:nth-child(1) {
    position: fixed;
    top: -20vh;
    left: -3vw;
    transform: scale(1.5);
  }
  .circle:nth-child(2) {
    position: fixed;
    top: -10vh;
    right: 25vw;
    z-index: -1;
  }
  .circle:nth-child(3) {
    position: fixed;
    bottom: -20vh;
    left: -3vw;
    transform: scale(1.25);
    z-index: -1;
  }
  .circle:nth-child(4) {
    position: fixed;
    bottom: -20vh;
    right: -5vw;
    transform: scale(1.25);
    z-index: -1;
  }
}
